import React, { useState, useEffect } from 'react'
import articlesConente from './article-content'
import AricleList from '../../components/ArticlesList'
import ErrorPage from '../404/ErrorPage'
import CommentsList from '../../components/CommentsList'
import UpvoteSection from '../../components/UpvoteSection'
import AddCommentForm from '../../components/AddCommentForm'


const Article = ({ match }) => {
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    const name = match.params.name;
    const article = articlesConente.find(item => item.name)
    const otherArticle = articlesConente.filter((item) => item.name !== name)


    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();

            setArticleInfo(body)
        }
        fetchData();
    }, [name])


    if (!article) {
        return <ErrorPage />
    }

    return (
        <div>
            <h1>{article.title}</h1>
            <UpvoteSection
                articleName={name}
                upvotes={articleInfo.upvotes}
                setArticleInfo={setArticleInfo}
            />
            {article.content.map((item, index) => {
                return <p key={index}>{item}</p>
            })}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm
                articleName={name}
                setArticleInfo={setArticleInfo} />
            <h3>Other Article</h3>
            <AricleList articles={otherArticle} />
        </div>
    )
}

export default Article
